@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Comfortaa";
  src: url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  padding: 0 26px;
}
@media (min-width: 1440px) {
  .container {
    width: 1128px;
    margin: 0 auto;
  }
}
.body {
  font-family: "Raleway", "Arial", sans-serif;
  margin: 0;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100%;
}
.title {
  font-family: "Comfortaa", "Arial", sans-serif;
  font-size: 36px;
  margin: 0;
  color: #0f805d;
  font-weight: 400;
  text-align: center;
}
@media (min-width: 768px) {
  .title {
    font-size: 48px;
    line-height: 54px;
  }
}
@media (min-width: 1440px) {
  .title {
    font-size: 52px;
  }
}
.logo {
  display: block;
}
.logo svg {
  fill: none;
}
@media (min-width: 768px) {
  .logo {
    width: 130px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .logo .logo__image {
    width: 60px;
    height: 60px;
    position: relative;
    left: -5px;
    bottom: 5px;
  }
}
.logo__text {
  display: none;
}
@media (min-width: 768px) {
  .logo__text {
    display: block;
  }
}
.checkbox-mark {
  position: relative;
  display: block;
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  flex: none;
  border-radius: 5px;
  border: 2px solid #0f805d;
}
.checkbox-mark--disabled {
  border: 2px solid #8e9892;
}
.radio-mark {
  position: relative;
  display: block;
  box-sizing: border-box;
  width: 21px;
  height: 21px;
  flex: none;
  border-radius: 50%;
  border: 2px solid #0f805d;
}
.radio-mark::before {
  content: "";
  display: block;
  position: absolute;
  width: 9px;
  height: 9px;
  background-color: #0f805d;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.input {
  font-family: "Raleway", "Arial", sans-serif;
  padding: 0;
  box-sizing: border-box;
  border: none;
  padding: 16px 0 16px 12px;
  border: 2px solid #0f805d;
  font-size: 16px;
  line-height: 20px;
  outline: none;
  color: #8e9892;
  border-radius: 20px;
}
.button {
  display: block;
  font-family: "Raleway", "Arial", sans-serif;
  background: none;
  border: none;
  text-decoration: none;
  padding: 14px 0;
  cursor: pointer;
  box-sizing: border-box;
  background-color: #6a107a;
  color: #fbfcfe;
  font-size: 20px;
  width: 100%;
  border-radius: 40px;
  text-align: center;
  letter-spacing: 1px;
}
.button:hover {
  background-color: #b22ec8;
}
.button:active {
  background: none;
  background-color: #6a107a;
  opacity: 0.6;
}
@media (min-width: 768px) {
  .button {
    padding: 16px 0;
  }
}
@media (min-width: 1440px) {
  .button {
    font-size: 28px;
    padding: 16px 0 18px 0;
  }
}
.button--small {
  padding: 14px 0;
  font-size: 20px;
}
.main-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: "Raleway";
  display: none;
}
@media (min-width: 768px) {
