  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, 32px);
  max-width: 136px;
  gap: 20px;
}
@media (min-width: 768px) {
  .social-list {
    max-width: 184px;
    grid-template-columns: repeat(auto-fit, 40px);
    gap: 32px;
  }
}
.social-list__link {
  display: flex;
  width: 32px;
  height: 32px;
  border: 1px solid #0f805d;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .social-list__link {
    width: 40px;
    height: 40px;
  }
}
.social-list__link svg {
  width: 20px;
  height: 20px;
  fill: #0f805d;
  margin: auto;
}
@media (min-width: 768px) {
  .social-list__link svg {
    width: 24px;
    height: 24px;
  }
}
.social-list__link:hover {
  border: 1px solid #7bdb0a;
}
.social-list__link:hover svg {
  fill: #7bdb0a;
}
.social-list__link:active {
  border: 1px solid #0f805d;
  opacity: 0.5;
}
.social-list__link:active svg {
  fill: #0f805d;
}
.main-footer {
  background-color: #f1f6fc;
  padding-top: 11px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .main-footer {
    padding: 25px 0 22px 0;
  }
}
.main-footer__social-list {
  margin-left: auto;
}
@media (min-width: 768px) {
  .main-footer__social-list {
    margin: 0;
    margin-left: 35px;
  }
}
.main-footer__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 427px) {
  .main-footer__container {
    column-gap: 40px;
  }
}
@media (min-width: 427px) {
  .main-footer__container {
    display: grid;
    grid-template-columns: 52px 136px 110px;
    place-content: center;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .main-footer__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.main-footer__text-link {
  font-family: "Raleway", "Arial", sans-serif;
  display: flex;
  column-gap: 8px;
  align-items: center;
  margin-left: auto;
  font-size: 11px;
  line-height: 13px;
  text-decoration: none;
  color: #000000;
  margin-top: 7px;
}
.main-footer__text-link span {
  flex: none;
}
@media (min-width: 427px) {
  .main-footer__text-link {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .main-footer__text-link {
    font-size: 16px;
    line-height: 19px;
    margin: 0;
  }
  .main-footer__text-link svg {
    width: 26px;
    height: 32px;
  }
}
